    border-right: 1px solid #ededed;
  }
  table.cart-contents .currency {
    margin-right: 0;
  }
  table.cart-contents .action {
    margin-top: 15px;
    float: none !important;
  }
  .cart-buttons .btn {
    display: block;
    float: none;
    margin: 0 0 20px 0;
    width: 100%;
