/* ENTRY */
.entry-deal {
  padding: 10px;
  border: 1px solid #ededed;
}
.entry-deal h2 {
  font-size: 18px;
}
.entry-media {
  height: 250px;
  position: relative;
  display: block}
.deal-entry {
  padding: 0 0 0;
  float: left;
  width: 100%;
  background-color: #FFF;
  zoom: 1;
  margin-bottom: 30px;
  position: relative;
  box-shadow: 0px 2px 3px -1px rgba(151, 171, 187, 0.7);
  transition: all .3s;
}
.deal-entry:hover {
  box-shadow: 0px 1px 10px -1px rgba(151, 171, 187, 0.7);
}
.deal-entry .image {
  margin: 0;
  max-width: 100%;
  display: block;
  position: relative;
}
.deal-entry .bought {
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 1;
  color: #FFFFFF;
  padding: 2px 15px;
  font-size: 13px;
}
.deal-entry .title {
  padding: 20px 15px 20px 15px;
  margin: 0;
  color: #000;
  font-size: 18px;
  line-height: 24px;
  font-weight: bold;
  text-shadow: 0 1px 0 #FFF;
  overflow: hidden;
}
.deal-entry.green .offer-discount,.deal-entry.green .bought {
  background-color: #77C84E;
}
.deal-entry.green .title {
  border-left: 5px solid #77C84E;
}
.deal-entry.orange .title {
  border-left: 5px solid #ff5722;
}
.deal-entry.orange .offer-discount,.deal-entry.orange .bought {
  background-color: #ff5722;
}
.deal-entry.orange .title {
  border-left: 5px solid #FF5722;
}
.deal-entry.blue .offer-discount,.deal-entry.blue .bought {
  background-color: #5677fc;
}
.deal-entry.blue .title {
  border-left: 5px solid #5677fc;
}
.deal-entry .title a {
  color: #272630;
  text-decoration: none;
}
.deal-entry .title a:hover {
  color: #ff5722;
}
.deal-entry .prices {
  padding: 0 0 10px 0;
  margin: 0;
  display: block;
}
.deal-entry .procent {
  padding: 0;
  margin: 0;
  font-size: 29px;
  line-height: 30px;
  float: left;
  color: #ff5722;
  font-weight: 500;
}
.deal-entry.green .procent {
  color: #77C84E;
}
.deal-entry.orange .procent {
  color: #FF0623;
}
.deal-entry.blue .procent {
  color: #5677fc;
}
.discount-red {
  background: #ff5722;
}
.discount-green {
  background: #77C84E;
}
.discount-blue {
  background: #5677fc;
}
.deal-entry .price {
  padding: 0;
  margin: 0;
  color: #333;
  font-size: 14px;
  line-height: 30px;
  float: right;
}
